.container {
    display: flex;
    align-items: center;
    margin: 0 18rem;
    margin-bottom: 4rem;
    background-color: #fff;
    border-radius: 10px;
    font-family: "Inter", sans-serif;
    font-size: 16px;

  }
  
  .left {
    margin-right: 20px;
    display: flex;
    flex-direction: column;
    // background-color: aliceblue;
    div{
        border: solid  1px lightgray;
        border-radius: 100px;
        width: 2rem;
        height: 2rem;
        margin-bottom: 1.5rem;
        text-align: center;
        font-size:1.5rem;
        
    }
    img{
        padding-left: 2rem;
        
    }
    :nth-child(3){
        padding-left: 4rem;
    }
  }
  
  .right {
    margin-left: 20px;
    // background-color: #0056b3;
    width:27vh;
  }
  
  .center {
    // background: blueviolet;
    // flex: 1;
    display: flex;
    flex-direction: column;
    margin:0rem 2rem;
    padding-bottom: 2.8rem;
    width: 30rem;
    height: 15rem;
    font-weight: 400;
    gap: 5px;
    .sec_para{
        padding-left: 2rem;
    }
    
  }
  .showmore{
    display: flex;
    color: #1B88F4;

    .image{
        padding-top: 3px;
        padding-left: 8px;

    }
  }
  
  .inner-right {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #074786;
    margin-bottom: 5rem;
    svg{
        margin: 10px;
    }
  }
  
  /* Style the image container */
  .inner-right p {
    background: url("../../assets/Rectangle.png");
    width: 135px;
    height: 118px;
    border-radius: 0 0 12px 12px; /* Corrected from 'radius' to 'border-radius' */
  }
  
  .button_with_margin {
    margin: 1.5rem 0rem 1rem; // Adjust the margin as per your requirement
  }
  
  button {
    // padding: 10px 20px;
    background-color: #007bff;
    color: white;
    width: 100%;
    height: 2.7rem;
    max-width: 100%;
    border: none;
    border-radius: 10px;
    padding:3rem;
    cursor: pointer;
  transition: transform 0.3s; /* Add transition for the transform property */
  }
  
  button:hover {
    background-color: #0367d3;
    transform: scale(1.1); 
  }

  button:focus {
    outline: none; /* Remove the default focus outline */
  }

  @media only screen and (max-width: 768px) {
    .left{
      flex-direction: column;
    }
    .center{
      height: 100vh;
    }
  }
  